<template>

  <div class="langBox" v-if="currLang">

    <el-dropdown @command="handleCommand">

      <div class="lang flex align">
        <i :class="`langIcon ${currLang.type}`"></i> <span class="langType">{{currLang.name}}</span><i class="el-icon-arrow-down"></i>
      </div>
        
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item  :command="item" v-for="(item,index) in lang_actions" :key="index">{{item.name}}</el-dropdown-item>
      </el-dropdown-menu>

    </el-dropdown>
    

  </div>

</template>


<script>
  let timer;
  export default {
    props: ['codeData'],
    data() {
      return {
        show: false,
        currLang:null,//当前语言
      }
    },
    methods: {
      handleCommand(v) {
        this.currLang = v;
        this.set_lang(v.type);
      }
    },
    mounted() {
      this.currLang = this.lang_actions[0]
    },
    beforeDestroy() {

    },

  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped rel="stylesheet/scss" lang="scss">
  @import "../../styles/Val";
  .langBox {
    position: relative;
    min-width: 160px;
    text-align: right;
    
    .selectBox {
      position: absolute;
      width: 100%;
      background: white;
      top: 60px;
      left: 0;
      box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.3);
      border-radius: 4px;
      z-index: 11;

      >.lang {
        padding: 15px 10px;

        &:hover {
          background: rgba(0, 0, 0, 0.03);
        }
      }
    }

    .lang {
      cursor: pointer;
    }

    .langType {
      margin: 0 5px;
      font-size: 16px;
      color: $nav_color;
    }

    .rowDownIcon {
      transition: all 0.3s;
      -webkit-transition: all 0.3s;

      &.tr {
        transform: rotate(180deg);
        -webkit-transform: rotate(180deg);
      }
    }

    .langIcon {
      width: 30px;
      height: 30px;
      background-size: 100% 100% !important;
      // border: 1px solid rgba(0, 0, 0, 0.1);
      border-radius: 100px;
      position: relative;
      top: 1px;
      &.zh_cn {
        background: url('../../assets/img/zg.png');
      }

      &.ko_kr {
        background: url('../../assets/img/hg.png');
      }

      &.en_us {
        background: url('../../assets/img/mg.png');
      }

      &.ja_jp {
        background: url('../../assets/img/rb.png');
      }
    }
  }

</style>



