<template>
  <div class="proItem flex between animated fadeIn" :class="detail?'detail':'shadownItemBox'" style="align-items:stretch;">

    <div class="img">
      <img src="../../assets/img/pro.png" :class="detail?'detail':''" alt="" srcset="">
    </div>
    <!-- img -->

    <div class="rightContent">

      <div class="flex align between" style="margin:20px 0;">

        <div class="pair flex align">
          <div class="from flex align"><i class="iconImg"
              :class="item.productName"></i>&nbsp;<span>{{item.productName}}</span></div>
          <div class="row"></div>
          <div class="to flex align"><i class="iconImg xcion"></i>&nbsp;<span>WMC</span></div>
        </div>
        <!-- pair -->

        <div class="goBtn hoverTr3" v-if="!detail" @click="gopage(`/exchangeDetail?${qsParams(item)}}`)">立即认购<i class="btnRight"></i>
        </div>

      </div>
      <!-- flex -->

      <div class="tagBox">
        <el-tag type="success" size="small" v-if="item.rebate>0 && item.rebate<1">认购返利：{{item.rebate * 100}}%</el-tag>
        <el-tag type="success" size="small" v-if="item.rebate>1">固定返现：{{item.rebate}}</el-tag>
        <el-tag type="danger" size="small" v-if="item.discount>0 && item.discount<1">认购折扣：{{item.discount * 10}} 折
        </el-tag>
      </div>

      <!-- <hr style="margin-bottom:0;"> -->

      <!-- <div class="pText">{{item.desc || '暂无描述'}}</div> -->
      <!-- pText -->

      <div class="dataGroup flex wrap" style="margin-top:10px;">
        <div class="item"><b>{{Number(item.total) - Number(item.current)}} </b><small>剩余额度 /
            {{item.productName}}</small></div>
        <div class="item"><b>{{item.max}} </b><small>最高认购额度 / {{item.productName}}</small></div>
        <div class="item"><b>{{item.min}} </b><small>最低认购额度 / {{item.productName}}</small></div>
        <!-- <div class="item"><b>{{item.lockDay}}</b><small>锁仓周期 / 天</small></div> -->
        <div class="item"><b><span>{{item.startTime}}</span></b><small>认购开始时间</small></div>
        <div class="item"><b><span>{{item.endTime}}</span></b><small>认购结束时间</small></div>
      </div>
      <!-- dataGroup -->


    </div>
    <!-- rightContent -->

  </div>
  <!-- proItem -->
</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'
  export default {
    props: ['item','detail'],
    data() {
      return {

      }
    },
    methods: {

    },
    mounted() {} //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../../styles/Val";

  .proItem {
    padding: 0 0;
    overflow: hidden;
    margin-bottom: 50px;
    &.detail{
        margin-bottom: 0;
    }

    .tagBox{
        .el-tag{
            margin-right:10px;
        }
        margin-top:25px;
        min-height:30px;
        border-bottom: 1px solid rgba(0,0,0,0.06);
        padding-bottom: 20px;
    }

    .img {
      width: 400px;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        display: block;
        &.detail{
          border-radius: 4px;
        }
      }
    }

    .rightContent {
      flex: 1;
      padding: 20px 50px;

      .pair {
        .row {
          background: url('../../assets/img/rightG.png') center no-repeat;
          background-size: 100% auto;
          width: 60px;
          height: 30px;
          margin: 0 25px;
        }

        .iconImg {
          width: 35px;
          height: 35px;
        }

        span {
          font-size: 25px;
          font-weight: 500;
          margin-left: 10px;
        }
      }

      .goBtn {
        border: 1px solid $them_color;
        display: block;
        cursor: pointer;
        color: $them_color;
        font-size: 16px;
        padding: 10px 20px;
        font-weight: 600;
        border-radius: 2px;
        font-weight: normal;

        i {
          display: inline-block;
          width: 25px;
          height: 15px;
          background: url('../../assets/img/right@2x.png') center no-repeat;
          background-size: 100% auto;
          position: relative;
          top: 1px;
          margin-left: 10px;
        }
      }

      .pText {
        color: $them_color_gray_light;
        font-size: 14px;
        display: block;
        padding: 30px 0;
        border-bottom: 1px solid #E9E9E9;
        margin-bottom: 10px;
      }

      .dataGroup {
        .item {
          width: 33%;
          margin-top: 20px;

          b {
            display: block;
            font-size: 20px;
            font-weight: normal;

            span {
              font-size: 14px;
            }
          }

          small {
            color: $them_color_gray_light;
            font-size: 14px;
          }
        }
      }
    }
  }

</style>



