<template>
  <div class="banner">

    <div class="text flex align center">WMC&nbsp;<small>|&nbsp;{{text}}</small></div>

    <img src="../../assets/img/pageBgMin.png" alt="">

  </div>
</template>

<script>
  import {
    mapMutations,
    mapState
  } from 'vuex'

  export default {
    props: ['text'],
    data() {
      return {
          swiperOption: { 
            // 滚动条                   
            scrollbar: {                        
                el: '.swiper-scrollbar',                    
            }, 
            // 上一张，下一张                   
            navigation: {                        
                nextEl: '.swiper-button-next',                        
                prevEl: '.swiper-button-prev',                    
            }
        }
      }
    },
    computed: {
      ...mapState(['userInfo']),
      user() {
        return this.userInfo ? this.userInfo.user : null;
      }, //user
    },
    methods: {

    },
    mounted() {

    }
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../../styles/Val";

  .banner {
    position: relative;
    img{
      width: 100%;
    }
    .text{
      color: white;
      text-align: center;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      font-size: 50px;
      font-style: oblique;
      letter-spacing: 3px;
      opacity: 0.7;
      small{
        font-style: oblique;
        font-style: 40px;
      }
    }
  }

</style>



