<template>
  <div>
    <div id="Nav" :class="`${classHome}`">
      <div class="mainContent flex align between">

        <div class="mavList flex align">
          <div class="logo cursor" @click="gopage('/home')">WMC</div>
          <div class="li" :class="{active : page=='home' }" @click="gopage('/home')">首页</div>
          <div class="li" :class="{active : page=='exchange' }" @click="gopage('/exchange')">产品</div>
          <div class="li" @click="openLink('static/paftest.pdf')">白皮书</div>
          <div class="li" :class="{active : page=='ecology' }" @click="gopage('/ecology')">生态</div>
          <div class="li" :class="{active : page=='help' }" @click="gopage('/help')">帮助</div>
        </div>
        <!-- mavList -->

        <div class="action flex align">


          <el-dropdown @command="handleCommand" v-if="userInfo">

            <div class="userName flex align">
              <img :src="userInfo.user.userHead ? setImgUrl(userInfo.user.userHead) : headImg " alt="">
              <span>{{userInfo.user.userName}} <i class="el-icon-arrow-down"></i> </span>
            </div>

            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="/userCenter">个人中心</el-dropdown-item>
              <el-dropdown-item command="loginOut">退出登录</el-dropdown-item>
            </el-dropdown-menu>

          </el-dropdown>



          <div class="login" @click="gopage('/login')" v-if="!userInfo">登录</div>
          <div class="register" @click="gopage('/register')" v-if="!userInfo">注册</div>
          <setLang />
        </div>
        <!-- action -->

      </div>
    </div>
    <!-- index -->
    <div style="height:70px;" v-if="page!='home'"></div>
  </div>
</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'

  import setLang from '@com/common/setLang'

  import headImg from '../assets/img/userIocn.png'

  export default {
    props: ['page', 'className'],
    data() {
      return {
        headImg,
        classHome:null
      }
    },
    components: {
      setLang
    },
    computed: {
      ...mapState(['userInfo'])
    },
    methods: {
      ...mapMutations(['setUserInfo', 'setToken']),
      handleCommand(v) {
        if (v == 'loginOut') {
          this.gopage_re('/home');
          setTimeout(() => {
            this.setUserInfo(null);
            this.setToken(null);
            localStorage.removeItem('vuex');
            location.reload();
          }, 500);
          return;
        }
        this.gopage(v);
      }, //handleCommand
      handleScroll(e) {
        var _this = this
        window.addEventListener('scroll', function () {
          var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
          if (scrollTop > 100) {
            _this.classHome = null;
          }else{
            _this.classHome = _this.className;
          }
        })
      } //handleScroll
    },
    mounted() {
      this.classHome = this.className;
      this.handleScroll();
    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";

  #Nav {
    background: #1d2747;
    transition: all 0.3s;

    &.homeTop {
      background: -webkit-gradient(linear, left top, right top, from(rgba(5, 12, 42, 0)), to(rgba(5, 12, 42, .5)));
      background: linear-gradient(90deg, rgba(5, 12, 42, 0), rgba(5, 12, 42, .5));
    }

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 11;

    .mainContent {
      padding: 15px 0;

      .mavList {
        .logo {
          color: white;
          font-weight: bold;
          font-size: 30px;
          margin-right: 25px;
        }

        .li {
          margin-left: 20px;
          padding: 5px 10px;
          font-size: 16px;
          cursor: pointer;
          color: $nav_color;

          &.active {
            color: white;
            // font-weight: bold;
          }

          &:hover {
            color: white;
          }
        }
      }

      .action {
        >div {
          cursor: pointer;
          font-size: 16px;
          margin-left: 10px;
          padding: 5px 20px;
          color: $nav_color;

          &.register {
            color: $nav_color;
            border: 1px solid $nav_color;
            border-radius: 4px;
          }
        }

      }
    }
  }

  .userName {
    font-weight: bold;

    * {
      font-weight: 500;
    }

    img {
      display: block;
      width: 30px;
      height: 30px;
      border-radius: 100px;
      margin-right: 10px;
      background: rgba(255,255,255,0.3);
    }
  }

</style>



