<template>

  <div>

    <div class="val">
      <!-- <span>+ {{value}} <i class="el-icon-arrow-down"></i> </span> -->
      <el-select v-model="value" filterable placeholder="请选择">
        <el-option v-for="(item,index) in regionCode" :key="index" :label="`+ ${item.tel}-${item.en}`" :value="item.tel">
        </el-option>
      </el-select>
    </div>

  </div>

</template>


<script>
  import regionCode from '../../util/regionCode';
  export default {
    props: ['default'],
    data() {
      return {
        regionCode,
        value: '+86'
      }
    },
    methods: {

    },
    mounted() {
      this.value = this.default || '+86';
    },
    watch:{
        value(val){
            this.$emit('setVal',val)
        }
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped rel="stylesheet/scss" lang="scss">
    .val{
        position: relative;
        width: 150px;
        height: 50px;
        border-right: 1px solid rgba(0, 0, 0, 0.1);
        text-align: center;
        line-height: 50px;
        font-weight: bold;
        margin-right: 10px;
        span{
            font-size: 18px;
            i{
                display: inline-block;
                margin-left: 5px;
                opacity: 0.8;
                font-size: 14px;
            }
        }
        /*.el-select{
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
        }*/
    }
</style>



