<template>
  <div v-loading="load">
    <a class="btn_border_them getCode" v-if="second">{{second}} {{$t('wallet.getCode.text_timer')}}</a>
    <a class="btn_border_them getCode pointer" v-else @click="getCode()">{{$t('wallet.getCode.text_get')}}</a>
  </div>
</template>


<script>
  let timer;
  import {
    send_message
  } from '../../data/apis';
  import {
    validatePhoneNum
  } from '../../util';
  export default {
    props: ['codeData'],
    data() {
      return {
        second: null, //倒计时
        load:false
      }
    },
    methods: {

      getCode() { //获取验证码
        if (!this.codeData.number) {
          this.$message.error('请输入电话或邮箱号码');
          return false;
        }
        let params = {
          "to": this.codeData.number,
          "func": this.codeData.type
        }
        this.load = true;
        send_message(params).then(v => {
          this.$message.success(v.message);
          this.setGetCodeStatue();
          this.load = false;
        }).catch(e=>{
          this.load = false;
        })
      }, //getCode
      setGetCodeStatue() { //设置获取验证码状态
        this.second = 60;
        timer = setInterval(() => {
          if (this.second > 1) {
            --this.second;
          } else {
            this.second = null;
            clearInterval(timer);
          }
        }, 1000)
      }, //setGetCodeStatue

    },
    mounted() {
      
    },
    beforeDestroy(){
      clearInterval(timer);
    },

  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped rel="stylesheet/scss" lang="scss">
.getCode{
  font-weight: bold;
  color: #333333;
  &:hover{
    text-decoration: underline;
  }
}
</style>



